/* This example requires Tailwind CSS v2.0+ */
import React, { Fragment, useRef, useState } from 'react'
import { Dialog, Transition } from '@headlessui/react'
import { ViewGridAddIcon } from '@heroicons/react/outline'

export default function ModalAddTrashCan({ open, onClose, modalError, modalSuccess, onClick, onChangeId, onChangeLocation, loadingModal }) {
    const cancelButtonRef = useRef(null)

    return (
        <Transition.Root show={open} as={Fragment}>
            <Dialog as="div" className="fixed z-10 inset-0 overflow-y-auto" initialFocus={cancelButtonRef} onClose={onClose}>
                <div className="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
                    <Transition.Child
                        as={Fragment}
                        enter="ease-out duration-300"
                        enterFrom="opacity-0"
                        enterTo="opacity-100"
                        leave="ease-in duration-200"
                        leaveFrom="opacity-100"
                        leaveTo="opacity-0"
                    >
                        <Dialog.Overlay className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
                    </Transition.Child>

                    {/* This element is to trick the browser into centering the modal contents. */}
                    <span className="hidden sm:inline-block sm:align-middle sm:h-screen" aria-hidden="true">
                        &#8203;
                    </span>
                    <Transition.Child
                        as={Fragment}
                        enter="ease-out duration-300"
                        enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                        enterTo="opacity-100 translate-y-0 sm:scale-100"
                        leave="ease-in duration-200"
                        leaveFrom="opacity-100 translate-y-0 sm:scale-100"
                        leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                    >
                        <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full">
                            <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                                <div className="sm:flex sm:items-start">
                                    <div className="mx-auto flex-shrink-0 flex items-center justify-center h-12 w-12 rounded-full bg-red-100 sm:mx-0 sm:h-10 sm:w-10">
                                        <ViewGridAddIcon className="h-6 w-6 text-green-600" aria-hidden="true" />
                                    </div>
                                    <div className="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                                        <Dialog.Title as="h3" className="text-lg leading-6 font-medium text-gray-900">
                                            Add New Trash Can
                                        </Dialog.Title>
                                        <div className="mt-2">
                                            <pre>{'Trash Code'.padEnd(16)}:  <input type="text" onChange={onChangeId} className='border-2 px-2 ring-1 ring-gray-200 focus:outline-none focus:ring-1 focus:border-transparent focus:ring-blue-300 border-transparent rounded-md'></input></pre>
                                        </div>
                                        <div className="mt-1">
                                            <pre>{'Location Name'.padEnd(16)}:  <input type="text" onChange={onChangeLocation} className='border-2 px-2 ring-1 ring-gray-200 focus:outline-none focus:ring-1 focus:border-transparent focus:ring-blue-300 border-transparent rounded-md'></input></pre>
                                        </div>
                                        <Dialog.Description className="mt-3 text-sm">
                                            {modalError ?
                                                <ul class='list-outside list-disc ml-6 text-red-500'>
                                                    {modalError.map((data, index) => (
                                                        <>
                                                            <li>{data}</li>
                                                        </>
                                                    ))}
                                                </ul>
                                                : ''}
                                            {modalSuccess.length > 0 ?
                                                <p class='list-outside list-disc ml-6 text-green-500'>
                                                    {modalSuccess}
                                                </p>
                                                : ''}
                                        </Dialog.Description>
                                    </div>
                                </div>
                            </div>
                            <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
                                {!loadingModal ?
                                    <button
                                        type="button"
                                        className="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-green-500 text-base font-medium text-white hover:bg-green-700 transition-colors duration-100 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 sm:ml-3 sm:w-auto sm:text-sm"
                                        onClick={onClick}
                                    >
                                        Add
                                    </button>
                                    :
                                    <button
                                        type="button"
                                        className="w-full inline-flex justify-center rounded-md border border-transparent  shadow-sm px-4 py-2 bg-green-300 text-base font-medium text-white hover:bg-green-700 transition-colors duration-100 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 sm:ml-3 sm:w-auto sm:text-sm"
                                        onClick={onClick}
                                        disabled>
                                        <div className="flex justify-center items-center h-full space-x-2 ">
                                            <div className="w-2 h-2 bg-blue-400 rounded-full animate-bounce delay-1"></div>
                                            <div className="w-2 h-2 bg-blue-400 rounded-full animate-bounce delay-2"></div>
                                            <div className="w-2 h-2 bg-blue-400 rounded-full animate-bounce delay-3"></div>
                                        </div>
                                    </button>
                                }
                                <button
                                    type="button"
                                    className="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm"
                                    onClick={onClose}
                                    ref={cancelButtonRef}
                                >
                                    Cancel
                                </button>
                            </div>
                        </div>
                    </Transition.Child>
                </div>
            </Dialog>
        </Transition.Root>
    )
}